@import 'variables';
@import 'commons';

.toggleButtons {
  @include displayCentered;
  background: $main_text_area_background;
  border-radius: 3px;
  overflow: hidden;

  .toggleButton {
    @include displayCentered;
    flex: 1;
    height: inherit;
    cursor: pointer;
    font-size: $main_font_size;
    transition: 0.5s;

    &:hover {
      flex: 1.2; } }

  .toggled {
    background: $main_border_color;
    color: $additional_font_color; } }
